.cn-team-container.cn-grid.cn-overlay .cn-team-member {
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  z-index: 1; }
  .cn-team-container.cn-grid.cn-overlay .cn-team-member .cn-image-style {
