.cn-team-container.cn-grid.cn-overlay .cn-team-member-overlay-background {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  transition: background 1s, opacity .5s;
  opacity: 0;
  visibility: hidden; }

.cn-team-container.cn-grid.cn-overlay .cn-team-member-overlay-details {
  margin: 0 10px;
  transition: opacity .5s .25s;
  opacity: 0;
  visibility: hidden; }

.cn-team-container.cn-grid.cn-overlay .cn-team-member:hover .cn-team-member-overlay-background {
  background: rgba(255, 255, 255, 0.9);
  opacity: 1;
  visibility: visible; }

.cn-team-container.cn-grid.cn-overlay .cn-team-member:hover .cn-team-member-overlay-details {
  opacity: 1;
  visibility: visible; }

.cn-team-container.cn-list {
